import { Component, OnInit } from '@angular/core';
import { CusOperationHttpService } from '../../../http/custom-operation/custom-operation.http.service';
import { Message, ConfirmationService } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CommonHttpService } from '../../../../common/http/common.http.service';
import * as _ from 'lodash';
import { CALENDAR_CN, tableMessage } from 'app/@uisftech/common/constant/config';
import { CustomAllotBean } from 'app/pages/tzb/custom/custom-marketing/custom-manage/wait-allot/bean/customAllot.bean';

@Component({
  selector: 'app-wait-allot',
  templateUrl: './wait-allot.component.html',
  styleUrls: ['./wait-allot.component.scss'],
  providers: [CusOperationHttpService, ConfirmationService, Commfunc]
})

export class WaiAllotComponent implements OnInit {

  tabMesg: any = tableMessage;
  msgs: Message[] = [];
  customAllotBean: CustomAllotBean = new CustomAllotBean();
  first: number = 0;
  totalNum: number = 0;
  tableData: any = [];
  display: boolean = false;
  checkAll: boolean = false;
  checkBox: any = [];
  chooseData: any = [];
  isHoldCrdCard: any = [];
  code: any;
  inValue: any;

  constructor(
    public confirmationService: ConfirmationService,
    public commfunc: Commfunc,
    public httpService: CusOperationHttpService,
  ) {
    this.codeValues();
    this.isHoldCrdCard = this.code['IsHoldCrdCard'];
  }

  ngOnInit() {

    this.customAllotBean.pageSize = 10;
    this.customAllotBean.pageNum = 1;
    this.first = 0;
    this.querySecle();
  }

  // 码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret;
  }

  querySecle() {
    this.customAllotBean.operationType = '1'; // 操作类型-分配
    this.customAllotBean.disType = '4'; // 分配场景编号-待分配
    this.customAllotBean.pageSize = 10;
    this.customAllotBean.pageNum = 1;
    this.first = 0;
    this.doQuery();
  }

  // 查询
  doQuery() {
    this.doClearCheck();
    let paramCustList = JSON.parse(this.commfunc.getSessionDataCH('waitAllotCustInfoList'));
    let paramBatchNo = this.commfunc.getSessionDataCH('waitAllotBatchNo');
    if (paramCustList == undefined || paramCustList == null) {
      // 未执行过分配直接查询
      this.customAllotBean.waitCustList = undefined;
      this.customAllotBean.batchNo = undefined;
    } else {
      // 执行过分配增加查询条件
      this.customAllotBean.waitCustList = paramCustList;
      this.customAllotBean.batchNo = paramBatchNo;
    }
    this.httpService.queryCustManageCustInfo(this.customAllotBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tableData = data.custList;
        this.totalNum = data.total;
        if (data.isEmptyCache == '0') {
          let param = JSON.parse(this.commfunc.getSessionDataCH('waitAllotCustInfoList'));
          if (param != undefined && param != null) {
            this.commfunc.clearSession('waitAllotCustInfoList');
            this.commfunc.clearSession('waitAllotBatchNo');
            this.customAllotBean.waitCustList = undefined;
            this.customAllotBean.batchNo = undefined;
          }
        }
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
    })
  }

  // 清除
  doClearCheck() {
    this.chooseData = [];
    this.checkAll = false;
  }

  // 分页
  paginate(event) {
    this.customAllotBean.pageSize = event.rows * 1;
    let currentPage = event.page + 1;
    this.customAllotBean.pageNum = currentPage;
    this.first = (currentPage - 1) * this.customAllotBean.pageSize;
    this.doQuery();
  }

  // 全选按钮
  checkAllClick() {
    if (this.tableData) {
      for (let i = 0; i < this.tableData.length; i++) {
        if (this.checkAll == true) {
          this.tableData[i].checkBox = true;
          this.chooseData.push(this.tableData[i]);
        }
        else {
          this.chooseData = [];
          this.tableData[i].checkBox = false;
        }
      }
    }
  }

  // 单选按钮
  checkBoxClick(i, data) {
    if (data.checkBox) {
      this.chooseData.push(data)
    } else {
      this.chooseData.forEach((item, i) => {
        if (item == data) {
          this.chooseData.splice(i, 1)
        }
      });
    }
    if (this.chooseData.length == this.tableData.length) {
      this.checkAll = true;
    } else {
      this.checkAll = false;
    }
  }

  // 分配
  doAllot(param) {
    if (param == 'more') {
      if (this.chooseData.length == 0) {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '请选择客户！' });
        return
      } else {
        this.inValue = {
          invalue: this.chooseData,
          type: '1'
        }
        this.display = true;
      }
    }
    else {
      this.inValue = {
        invalue: param,
        type: '1'
      }
      this.display = true;
    }
  }

  // 关闭
  doMoreAllotCall() {
    
    this.tableData = [];
    this.chooseData = [];
    this.display = false;
    this.querySecle();
  }

  // 点击叉号
  onHideClose() {
    this.tableData = [];
    this.chooseData = [];
    this.display = false;
    this.querySecle();
  }

  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
    // return true;
  }

  compileStr(cust){
    return this.commfunc.compileStr(cust)
  }
}
