import { Component, OnInit } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Router } from '@angular/router'

@Component({
  selector: 'custom-manage',
  templateUrl: './custom-manage.component.html',
  styleUrls: ['./custom-manage.component.scss']
})
export class CustomManageComponent implements OnInit {
 
  show: number = 1;

  constructor(
    public commfunc: Commfunc,
    public router: Router,
  ) {

  }

  ngOnInit() {
    
    let stu_1 = this.commfunc.permissionCheck('SID00001_P020_P002_P003_P011'); // 我要申领
    let stu_2 = this.commfunc.permissionCheck('SID00001_P020_P002_P003_P013'); // 我要转让
    let stu_3 = this.commfunc.permissionCheck('SID00001_P020_P002_P003_P015'); // 我要分配-单户分配
    let stu_4 = this.commfunc.permissionCheck('SID00001_P020_P002_P003_P017'); // 我要分配-批量分配
    let stu_5 = this.commfunc.permissionCheck('SID00001_P020_P002_P003_P019'); // 待分配客户
    let stu_6 = this.commfunc.permissionCheck('SID00001_P020_P002_P003_P021'); // 调整日志

    if (stu_1) {
      this.show = 1;
      this.router.navigate(['/pages/tzb/custom/custom-marketing/cus-manage/cus-do-apply'])
    } else if (stu_2) {
      this.show = 2;
      this.router.navigate(['/pages/tzb/custom/custom-marketing/cus-manage/cus-do-transfer'])
    } else if (stu_3) {
      this.show = 3;
      this.router.navigate(['/pages/tzb/custom/custom-marketing/cus-manage/cus-do-allot'])
    } else if (stu_4) {
      this.show = 4;
      this.router.navigate(['/pages/tzb/custom/custom-marketing/cus-manage/batch-allot'])
    } else if (stu_5) {
      this.show = 5;
      this.router.navigate(['/pages/tzb/custom/custom-marketing/cus-manage/wait-allot'])
    } else if (stu_6) {
      this.show = 6;
      this.router.navigate(['/pages/tzb/custom/custom-marketing/cus-manage/adjust-log'])
    }
  }

  ngOnChanges() {

  }

  // 切换标签
  allot(num) {
    this.show = num;
  }

  // 按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
    // return true;
  }
}
