  // 码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret;
  }

  // 查询初始化2
  querySecle2() {
    this.adjustLogBean.pageSize = 10;
    this.adjustLogBean.pageNum = 1;
    this.first = 0;
    this.doQuery();
  }

  // 查询初始化
