/**
 * @author fanhualing 2018-5-18 批量分配
 */
import { Component, OnInit } from '@angular/core';
import { Message } from 'primeng/primeng';
import { Router } from '@angular/router';
import { CusOperationHttpService } from 'app/pages/tzb/custom/http/custom-operation/custom-operation.http.service';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { BatchAllotBean, CustomBatchBean } from 'app/pages/tzb/custom/custom-marketing/custom-manage/batch-allot/bean/BatchAllot.bean';
import { TZB_CUS_DOWNLOAD, TZB_CUS_UPLOADCQ } from 'app/@uisftech/common/service/http.constant';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
  selector: 'app-batch-allot',
  templateUrl: './batch-allot.component.html',
  styleUrls: ['./batch-allot.component.scss'],
  providers: [CusOperationHttpService]
})
export class BatchAllotComponent implements OnInit {

  msgs: Message[] = [];
  tabMesg: any = tableMessage;
  batchAllotBean: BatchAllotBean = new BatchAllotBean();
  customBatchBean: CustomBatchBean = new CustomBatchBean();
  // 数据
  totalNum: number;
  successNum: number;
  failNum: number;
  approveNo: string;
  successList: any;
  failList: any;
  // 文件
  files: any = [];
  fileList: any = [];
  // 上传、下载
  DOWNLOAD: string = TZB_CUS_DOWNLOAD;
  UPLOAD: string = TZB_CUS_UPLOADCQ;
  // 模板、用户切换
  custType: string;
  Property1: boolean = false;
  Property2: boolean = false;
  Propertym: boolean = false;
  // 当前登录用户
  user1: any;
  user: any;
  userName: any;
  userId: any;
  orgId: any;
  orgName: any;
  // 选择员工
  userTreeDisplay: boolean = false;
  ownerTreeDisplay: boolean = false;
  ownerInValue: any;
  receiverTreeDisplay: boolean = false;
  receiverInValue: any;
  // 审批人
  waitSelUserList: any;
  aaaa: boolean = false;
  outVal: any;
  myTabMesg: any;
  // 权限控制
  permission: string;
  op: string;
  rzjg: string;//任职机构
  flagTip: string;
  PropertyJt: boolean = false;
  PropertyFh: boolean = false;
  custListNo: number = 0;
  batchPrimaryId: string; //模板导入成功缓存主键

  constructor(
    public httpService: CusOperationHttpService,
    public commfunc: Commfunc,
    public router: Router,
  ) {
    this.user1 = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    this.user = JSON.parse(this.commfunc.getSessionDataCH('chName'));
    if (this.user1.userId) {
      this.userId = this.user1.userId;
    }
    if (this.user1.orgId) {
      this.orgId = this.user1.orgId;
    }
    if (this.user.userName) {
      this.userName = this.user.userName;
    }
    if (this.user.orgName) {
      this.orgName = this.user.orgName;
    }

    // 权限控制
    let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    this.flagTip = s.targetId.charAt(s.targetId.length - 1);
    if (s.extParam.managePermission) {
      this.permission = s.extParam.managePermission;
    }
    if (s.extParam.operatingLevel) {
      this.op = s.extParam.operatingLevel; //层级
    }
    if (s.extParam.orgId) {
      this.rzjg = s.extParam.orgId;
    }
    if (this.op == 'GROUP') {
      this.PropertyFh = false;
      this.PropertyJt = true;
    } else if (this.op == 'BRANCH'){
      this.PropertyJt = false;
      this.PropertyFh = true;
    }
  }

  //初始化
  ngOnInit() {
    //判断是否有名单导入权限
    if(this.permissionCheck('SID00001_P020_P002_P003_P017_11')){
      this.custType = '1';
      this.changeCustType('1');
    }else if(this.permissionCheck('SID00001_P020_P002_P003_P017_13')){
      this.custType = '2';
      this.changeCustType('2');
    }
  }

  //更改查询条件
  changeCustType(param) {
    
    this.doEmpty();
    if (param == '1') {
      this.Property1 = true;
      this.Propertym = false;
      this.Property2 = false;
      if (this.files) {
        this.files = [];
      }
    }
    if (param == '2') {
      this.Property1 = false;
      this.Property2 = true;
      this.Propertym = false;
    }
  }

  // 按模板-文件上传
  load(event) {
    for (let file of event.files) {
      this.Propertym = false;
      this.files = [];
      this.files.push(file)
    }
    
    if (event.xhr.response) {
      this.fileList = JSON.parse(event.xhr.response);
      if (this.fileList.success == false) {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: this.fileList.message });
        this.files = [];
        this.Propertym = false;
        return
      }
    }

    if (this.fileList[0]) {
      this.customBatchBean.fileUrl = this.fileList[0].fileUrl;
      this.customBatchBean.fileName = this.fileList[0].fileName;
      this.doQueryList();
    }
  }

  // 按模板-查询导入客户信息
  doQueryList() {
    // 入参(‘分配’并按‘客户号分配’)标识
    this.customBatchBean.operationType = '1';
    this.customBatchBean.disType = '2';
    this.customBatchBean.ownerManagerId = undefined;
    this.Propertym = true;
    this.httpService.queryCustManageCustInfo(this.customBatchBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.approveNo = data.approveNo;
        this.failList = data.batchCustFailList;
        this.totalNum = data.total;
        this.successNum = data.successTotal;
        this.failNum = data.failTotal;
        this.custListNo = data.successTotal;
        this.batchPrimaryId = data.batchPrimaryId;
        this.batchAllotBean.approveNo = data.approveNo;
        this.batchAllotBean.custListNo = data.successTotal;
        this.batchAllotBean.batchPrimaryId = data.batchPrimaryId;
        this.Propertym = true;
      } else {
        this.Propertym = false;
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.Propertym = false;
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务异常' });
    })
  }

  // 按模板调整
  doAllotImport() {
    if (this.custListNo <= 0) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '分配客户列表为空' });
      return
    } else {
      // 是否走审批
      this.isGoApprove(this.approveNo, 'template');
    }
  }

  // 是否走审批
  isGoApprove(param, param2) {
    this.batchAllotBean.approveNo = this.approveNo;
    this.batchAllotBean.creater = this.userId;
    this.batchAllotBean.createrName = this.userName;
    this.batchAllotBean.createOrgId = this.orgId;;
    this.batchAllotBean.createOrgName = this.orgName;
    // 按模板
    if (param2 == 'template') {
      this.batchAllotBean.operationType = '1';
      this.batchAllotBean.disType = '2';
    }
    // 按用户
    else if (param2 == 'user') {
      this.batchAllotBean.operationType = '1';
      this.batchAllotBean.disType = '3';
    }
    let paramIn = param;
    // 无需走审批
    if (paramIn == '0') {
      this.doSubmit(param2);
    }
    // 需要走审批
    if (paramIn == '1') {
      this.waitSelUserList = this.commfunc.query('KHFPLC', {});
      if (!this.waitSelUserList.erro) {
          this.outVal = this.waitSelUserList.waitSelUserList;
          let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
          let a = commonHeader.extParam.specifyNextOpers;
          if (this.waitSelUserList.display == true && (a == undefined || a == '' || a == null)) {
            this.aaaa = true;  // 要选人
          }else{
            if (this.custType == '1') {
              this.doSubmit('template');
            }
            if (this.custType == '2') {
              this.doSubmit('user');
            }
          }
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: this.waitSelUserList.erro });
      }
    }
  }

  // 数据返回给后台
  doSubmit(param2) {
    this.httpService.custManagementBatchProcess(this.batchAllotBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (s.extParam.specifyNextOpers) {
          delete s.extParam.specifyNextOpers
          let commonHeader = s;
          this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        }
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
        this.doEmpty();
        if (param2 == 'template') {
          this.Property1 = true;
          this.custType = '1';
          this.changeCustType('1');
        } else {
          this.Property2 = true;
          this.custType = '2';
          this.changeCustType('2');
        }
      } else {
        let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (s.extParam.specifyNextOpers) {
          delete s.extParam.specifyNextOpers
          let commonHeader = s;
          this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        }
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
        this.doEmpty();
        if (param2 == 'template') {
          this.Property1 = true;
          this.custType = '1';
          this.changeCustType('1');
        } else {
          this.Property2 = true;
          this.custType = '2';
          this.changeCustType('2');
        }
      }
    }, error => {
      let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
      if (s.extParam.specifyNextOpers) {
        delete s.extParam.specifyNextOpers
        let commonHeader = s;
        this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
      }
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
      this.doEmpty();
      if (param2 == 'template') {
        this.Property1 = true;
        this.custType = '1';
        this.changeCustType('1');
      } else {
        this.Property2 = true;
        this.custType = '2';
        this.changeCustType('2');
      }
    })
  }

  // 选择员工
  openTree(param) {
    this.ownerTreeDisplay = false;
    this.receiverTreeDisplay = false;
    this.userTreeDisplay = false;
    if (param == 'user') {
      this.ownerTreeDisplay = true;
      this.userTreeDisplay = !this.userTreeDisplay;
      // 如果是分行-判断村镇还是台行，如果是支行或部门，传任职机构
      if (this.op == 'BRANCH') {
        if (this.flagTip == '0') {
          this.ownerInValue = 'T0000';
        } else {
          this.ownerInValue = 'C0000';
        }
      } else {
        this.ownerInValue = this.rzjg;
      }

    }
    if (param == 'user2') {
      this.receiverTreeDisplay = true;
      this.userTreeDisplay = !this.userTreeDisplay;
      this.receiverInValue = {
        orgId: this.rzjg,
        tellerStatus: '0',  //tellerStatus 0:正常 1:不正常
      }
    }
  }

  // 关闭-员工模态框
  workerCall(item, param) {
    if (param == 'user') {
      if (item.length > 0) {
        this.batchAllotBean.ownerManagerId = item[0].tellerId;
        this.batchAllotBean.ownerManagerName = item[0].tellerName;
        this.batchAllotBean.ownerOrgId = item[0].orgId;
        this.batchAllotBean.ownerOrgName = item[0].orgName;
      }
      this.ownerTreeDisplay = false;
      this.userTreeDisplay = false;
    }
    if (param == 'user2') {
      if (item.length > 0) {
        this.batchAllotBean.receiver = item[0].tellerId;
        this.batchAllotBean.receiverName = item[0].tellerName;
        this.batchAllotBean.receiveOrgId = item[0].orgId;
        this.batchAllotBean.receiveOrgName = item[0].orgName;
      }
      this.receiverTreeDisplay = false;
      this.userTreeDisplay = false;
    }
  }

  // 按用户调整
  doAllot() {
    // 判断被调整人
    if (!this.batchAllotBean.ownerManagerId) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '被调整员工不能为空' });
      return
    }
    if (!this.batchAllotBean.receiver) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '接收员工不能为空' });
      return
    }
    // 操作人
    this.batchAllotBean.creater = this.userId;
    this.batchAllotBean.createrName = this.userName;
    this.batchAllotBean.createOrgId = this.orgId;
    this.batchAllotBean.createOrgName = this.orgName;

    this.batchAllotBean.operationType = '1';//按用户查询
    this.batchAllotBean.disType = '3';//按用户查询

    this.httpService.custManagementBatchHandle(this.batchAllotBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        
        this.approveNo = data.approveNo;
        this.custListNo = data.custListNo;
        this.batchAllotBean.approveNo = data.approveNo;
        this.batchAllotBean.custListNo = data.custListNo;
        this.isGoApprove(this.approveNo, 'user');
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务异常' });
    })
  }

  // 选人回调
  outValueCall(event) {
    this.outVal = event;
    let ss = '';
    this.outVal.forEach(item => {
      ss += item.tellerId + ','
    });
    let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    commonHeader.extParam['specifyNextOpers'] = ss;
    this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
    if (this.custType == '1') {
      this.doSubmit('template');
    }
    if (this.custType == '2') {
      this.doSubmit('user');
    }
  }

  // 关闭选人框
  outDisplayCall(event) {
    this.aaaa = false;
  }

  // 关闭模态框
  onHideClose() {
    this.Propertym = false;
    if (this.files) {
      this.files = [];
    }
  }

  //清空
  doEmpty() {
    this.batchAllotBean = new BatchAllotBean();
    this.Propertym = false;
    this.Property2 = false;
    this.Property1 = false;
    this.totalNum = null;
    this.successNum = null;
    this.failNum = null;
    this.failList = [];
    this.custListNo = 0;
    if (this.files) {
      this.files = [];
    }
  }

  // 点击客户号跳转360视图
  toCustView(data) {
    this.commfunc.setSessionData('custNo', data.custNo);
    this.router.navigate(['pages/custom-view']);
  }

  // 按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id);
  }

  compileStr(cust){
    return this.commfunc.compileStr(cust)
  }
}
