/**
 * @author fanhualing 2018-5-9 客户转让
 */
import { Component, OnInit } from '@angular/core';
import { Message } from 'primeng/primeng';
import { Router } from '@angular/router';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { CusOperationHttpService } from 'app/pages/tzb/custom/http/custom-operation/custom-operation.http.service';
import { CusDoTransferBean, CustomTransferExamBean } from 'app/pages/tzb/custom/custom-marketing/custom-manage/cus-do-transfer/bean/CusDoTransfer.bean';

@Component({
  selector: 'cus-do-transfer',
  templateUrl: './cus-do-transfer.component.html',
  styleUrls: ['./cus-do-transfer.component.scss'],
  providers: [CusOperationHttpService, Commfunc]
})
export class CusDoTtransferComponent implements OnInit {

  tabMesg: any = tableMessage;
  msgs: Message[] = [];
  cusDoTransferBean: CusDoTransferBean = new CusDoTransferBean();
  customTransferDialogBean: CustomTransferExamBean = new CustomTransferExamBean();
  first: number = 0;
  totalNum: number = 0;
  tablelist: any = [];
  transferCert: any = [];
  cert_type: any = [];
  isHoldCrdCard: any = [];
  chooseData: any = [];
  inValue: any;
  display: boolean = false;
  OwnerDisplay: boolean = false;
  outVal: any;
  aaaa: boolean = false;
  outValBean: any;
  code: any;
  receiverName: string;
  iskzh: string = '';
  iszlxr: string = '';
  iskfh: string = '';
  approveNo: string;
  dfCustno: string = '';
  dfOrg: string = '';
  chooseWork: boolean = false;
  waitSelUserList: any;
  choosePeople: boolean = false;
  user1: any;
  user: any;
  userName: any;
  userId: any;
  orgId: any;
  orgName: any;
  ownerInValue: any;

  constructor(
    public httpService: CusOperationHttpService,
    public commfunc: Commfunc,
    public router: Router,
  ) {
    this.codeValues();
    this.cert_type = this.code['CERT_TYPE'];
    this.cert_type.forEach(element => {
      if (element.value != '') {
        this.transferCert.push(element);
      }
    });
    this.isHoldCrdCard = this.code['IsHoldCrdCard'];

    this.user1 = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    this.user = JSON.parse(this.commfunc.getSessionDataCH('chName'));
    if (this.user1.userId) {
      this.userId = this.user1.userId;
    }
    if (this.user1.orgId) {
      this.orgId = this.user1.orgId;
    }
    if (this.user.userName) {
      this.userName = this.user.userName;
    }
    if (this.user.orgName) {
      this.orgName = this.user.orgName;
    }
  }

  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret;
  }

  ngOnInit() {
  }

  // 查询
  doQuery() {
    // 清空数据
    this.tablelist = [];
    this.chooseData = [];
    /// 校验客户号
    var reg = /^[0-9]*$/;
    if (
      this.cusDoTransferBean.custNo == undefined ||
      this.cusDoTransferBean.custNo == ''
    ) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '请输入客户号!' });
      return
    }
    if (this.cusDoTransferBean.custNo != undefined) {
      if (this.cusDoTransferBean.custNo != '') {
        if (!reg.test(this.cusDoTransferBean.custNo)) {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: '客户号只能为数字' });
          return
        }
        if (this.cusDoTransferBean.custNo.length != 10) {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: '请输入10位数字的客户号' });
          return
        }
      }
    }
    this.cusDoTransferBean.operationType = '3'; // 操作类型-转让
    this.cusDoTransferBean.disType = ''; // 分配场景编号-按客户号分配
    this.httpService.queryCustManageCustInfo(this.cusDoTransferBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        if(data.custList.length > 0){
          this.tablelist = data.custList;
          this.chooseData = this.tablelist;
          if(this.tablelist[0].ownerManagerName == '' || this.tablelist[0].ownerManagerName == null ){
            this.OwnerDisplay = false;
          }else{
            this.OwnerDisplay = true;
          }
        }else{
          this.chooseData = [];
        }
      }
      else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
    }
    )
  }

  // 转让
  doAllot() {
    if (this.chooseData.length == 0) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '请选择客户！' });
      return
    } else {
      this.doChoosePeople();
    }
  }

  // 提交
  doChoosePeople() {

    if (this.customTransferDialogBean.receiver == '' || this.customTransferDialogBean.receiver == undefined) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '请选择接收人！' });
      return
    }
    // 入参(‘转让’并按‘客户号分配’)标识
    this.customTransferDialogBean.operationType = '3';
    this.customTransferDialogBean.disType = '';
    // 接收人
    this.customTransferDialogBean.creater = this.userId;
    this.customTransferDialogBean.createrName = this.userName;
    this.customTransferDialogBean.createOrgId = this.orgId;
    this.customTransferDialogBean.createOrgName = this.orgName;
    // this.customTransferDialogBean.receiveOrgName = this.orgName;
    // this.customTransferDialogBean.receiveOrgId = this.orgId;
    // 客户信息
    this.customTransferDialogBean.custList = this.chooseData;
    // 判断是否走审批
    this.httpService.custManageApprCandidate(this.customTransferDialogBean).subscribe(data => {
      if (data.returnCode.code == 'success') {

        this.dfCustno = data.dfCustno; //归属客户经理ID/主联系人ID 
        this.dfOrg = data.dfOrg; //归属机构ID/主联系人机构ID
        this.approveNo = data.approveNo; // 审批标识	0:无需审批,1:需要审批
        this.iszlxr = data.iszlxr; //是否有主联系人 0:无主,1:有主
        this.iskzh = data.iskzh; //是否跨支行 0:不跨行,1:跨行
        this.iskfh = data.iskfh; //是否跨分行 0:不跨行,1:跨行
        this.customTransferDialogBean['approveNo'] = this.approveNo;
        this.customTransferDialogBean['iszlxr'] = this.iszlxr;
        this.customTransferDialogBean['iskzh'] = this.iskzh;
        this.customTransferDialogBean['iskfh'] = this.iskfh;
        this.customTransferDialogBean['dfCustno'] = this.dfCustno;
        this.customTransferDialogBean['dfOrg'] = this.dfOrg;
        if (data.approveNo == '1') {
          this.waitSelUserList = this.commfunc.query('KHZRLC', { iszlxr: this.iszlxr, iskzh: this.iskzh, iskfh: this.iskfh, dfCustno: this.dfCustno, dfOrg: this.dfOrg });
          if (!this.waitSelUserList.erro) {
              this.outVal = this.waitSelUserList.waitSelUserList;
              let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
              let a = commonHeader.extParam.specifyNextOpers;
              if (this.waitSelUserList.display == true && (a == undefined || a == '' || a == null)) {
                this.aaaa = true;  // 要选人
              }else{
                this.doSubmit();
              }
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: this.waitSelUserList.erro });
          }
        } else {
          this.doSubmit();
        }
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
        this.doEmpty();
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
      this.doEmpty();
    })
  }

  // 点击叉号
  onHideClose(param) {
    if (param == 'user') {
      this.display = false;
    }
    if (param == 'people') {
      this.aaaa = false;
      this.doEmpty();
    }
  }

  // 关闭选人框
  outDisplayCall(event) {
    this.aaaa = false;
    this.display = false;
  }

  // 选完人后回调
  outValueCall(event) {
    this.outVal = event;
    let ss = '';
    this.outVal.forEach(item => {
      ss += item.tellerId + ','
    });
    let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    commonHeader.extParam['specifyNextOpers'] = ss;
    this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
    this.doSubmit();
  }

  // 数据返回给后台
  doSubmit() {
    this.httpService.custManageDataLandHandle(this.customTransferDialogBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (s.extParam.specifyNextOpers) {
          delete s.extParam.specifyNextOpers
          let commonHeader = s;
          this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        }
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
        this.doEmpty();
      } else {
        let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (s.extParam.specifyNextOpers) {
          delete s.extParam.specifyNextOpers
          let commonHeader = s;
          this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        }
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
        this.doEmpty();
      }
    }, error => {
      let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
      if (s.extParam.specifyNextOpers) {
        delete s.extParam.specifyNextOpers
        let commonHeader = s;
        this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
      }
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
      this.doEmpty();
    })
  }

  // 选择员工
  chooseWorker() {
    this.display = true;
    this.ownerInValue = this.orgId;
  }

  // 员工回调
  perInfor(item) {
    this.display = false;
    if (item.length > 0) {
      this.customTransferDialogBean.receiverName = item[0].tellerName;
      this.customTransferDialogBean.receiver = item[0].tellerId;
      this.customTransferDialogBean.receiveOrgName = item[0].orgName;
      this.customTransferDialogBean.receiveOrgId = item[0].orgId;
    }
  }

  // 清空
  doEmpty() {
    this.tablelist = [];
    this.chooseData = [];
    this.customTransferDialogBean = new CustomTransferExamBean();
    this.cusDoTransferBean.custNo = undefined;
  }

  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
    // return true;
  }

  compileStr(cust){
    return this.commfunc.compileStr(cust)
  }
}

