/**
 * @author fanhualing 2018-5-9 客户分配
 */
import { Component, OnInit } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { ConfirmDialogModule, ConfirmationService } from 'primeng/primeng';
import { Message } from 'primeng/components/common/api';
import { CusOperationHttpService } from 'app/pages/tzb/custom/http/custom-operation/custom-operation.http.service';
import { CusDoAllotBean, CustomAllotExamBean } from 'app/pages/tzb/custom/custom-marketing/custom-manage/cus-do-allot/bean/CusDoAllot.bean';

@Component({
  selector: 'app-cus-do-allot',
  templateUrl: './cus-do-allot.component.html',
  styleUrls: ['./cus-do-allot.component.scss'],
  providers: [CusOperationHttpService, Commfunc, ConfirmationService]
})
export class CusDoAllotComponent implements OnInit {

  cusDoAllotBean: CusDoAllotBean = new CusDoAllotBean();
  customAllotDialogBean: CustomAllotExamBean = new CustomAllotExamBean();
  first: number = 0;//分页控制
  totalNum: number = 0;
  msgs: Message[] = [];
  tablelist: any = [];
  cert_type: any = [];
  isHoldCrdCard: any = [];
  chooseData: any = [];
  inValue: any;
  display: boolean = false;
  outValBean: any;
  code: any;
  user1: any;
  user: any;
  userName: any;
  userId: any;
  orgId: any;
  orgName: any;
  ownerInValue: any;
  receiverName: any;
  iskzh: string = '';
  iszlxr: string = '';
  iskfh: string = '';
  approveNo: string = '';
  dfCustno: string = '';
  dfOrg: string = '';
  waitSelUserList: any;
  outVal: any;
  aaaa: boolean = false;
  OwnerDisplay: boolean = false;
  orgTreeDisplay: boolean = false;
  userInValue: any;
  allotCert: any = [];
  receiveOrgName: string;
  receiveOrgId: string;

  constructor(
    public httpService: CusOperationHttpService,
    public commfunc: Commfunc,
  ) {
    this.codeValues();
    this.cert_type = this.code['CERT_TYPE'];
    this.cert_type.forEach(element => {
      if (element.value != '') {
        this.allotCert.push(element);
      }
    });
    this.isHoldCrdCard = this.code['IsHoldCrdCard'];
    this.user1 = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    this.user = JSON.parse(this.commfunc.getSessionDataCH('chName'));
    if (this.user1.userId) {
      this.userId = this.user1.userId;
    }
    if (this.user1.orgId) {
      this.orgId = this.user1.orgId;
    }
    if (this.user.userName) {
      this.userName = this.user.userName;
    }
    if (this.user.orgName) {
      this.orgName = this.user.orgName;
    }
  }

  ngOnInit() {

  }

  // 码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret;
  }

  // 查询
  doQuery() {
    // 清空数据
    this.tablelist = [];
    this.chooseData = [];
    // 校验客户号
    var reg = /^[0-9]*$/;
    if (
      this.cusDoAllotBean.custNo == undefined ||
      this.cusDoAllotBean.custNo == ''
    ) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '请输入客户号!' });
      return
    }
    if (this.cusDoAllotBean.custNo != undefined) {
      if (this.cusDoAllotBean.custNo != '') {
        if (!reg.test(this.cusDoAllotBean.custNo)) {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: '客户号只能为数字' });
          return
        }
        if (this.cusDoAllotBean.custNo.length != 10) {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: '请输入10位数字的客户号' });
          return
        }
      }
    }
    this.cusDoAllotBean.operationType = '1'; // 操作类型-分配
    this.cusDoAllotBean.disType = '1'; // 分配场景编号-按客户号分配
    this.httpService.queryCustManageCustInfo(this.cusDoAllotBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        if(data.custList.length > 0){
          this.tablelist = data.custList;
          this.chooseData = this.tablelist;
          if(this.tablelist[0].ownerManagerName == '' || this.tablelist[0].ownerManagerName == null ){
            this.OwnerDisplay = false;
          }else{
            this.OwnerDisplay = true;
          }
        }else{
          this.chooseData = [];
        }
      }
      else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
    }
    )
  }

  // 单户分配
  doAllot() {
    if (this.chooseData.length == 0) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '请选择客户！' });
      return
    } else {
      // 选择审批人
      this.doChoosePeople();
    }
  }

  // 提交服务
  doChoosePeople() {

    if (this.receiveOrgName == '' || this.receiveOrgName == undefined || this.receiveOrgName == null) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '请选择接收机构！' });
      return
    }
    if (this.customAllotDialogBean.receiver == '' || this.customAllotDialogBean.receiver == undefined) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '请选择接收人！' });
      return
    }
    // 入参(‘分配’并按‘客户号分配’)标识
    this.customAllotDialogBean.operationType = '1';
    this.customAllotDialogBean.disType = '1';
    // 接收人
    this.customAllotDialogBean.creater = this.userId;
    this.customAllotDialogBean.createrName = this.userName;
    this.customAllotDialogBean.createOrgId = this.orgId;
    this.customAllotDialogBean.createOrgName = this.orgName;
    // 客户信息
    this.customAllotDialogBean.custList = this.chooseData;
    // 判断是否走审批
    this.httpService.custManageApprCandidate(this.customAllotDialogBean).subscribe(data => {
      if (data.returnCode.code == 'success') {

        this.dfCustno = data.dfCustno; 
        this.dfOrg = data.dfOrg;
        this.approveNo = data.approveNo;
        this.iszlxr = data.iszlxr;
        this.iskzh = data.iskzh;
        this.iskfh = data.iskfh;
        this.customAllotDialogBean['approveNo'] = this.approveNo;
        this.customAllotDialogBean['iszlxr'] = this.iszlxr;
        this.customAllotDialogBean['iskzh'] = this.iskzh;
        this.customAllotDialogBean['iskfh'] = this.iskfh;
        this.customAllotDialogBean['dfCustno'] = this.dfCustno;
        this.customAllotDialogBean['dfOrg'] = this.dfOrg;

        if (data.approveNo == '1') {
          this.waitSelUserList = this.commfunc.query('KHFPLC', { iszlxr: this.iszlxr, iskzh: this.iskzh, iskfh: this.iskfh, dfCustno: this.dfCustno, dfOrg: this.dfOrg });
          if (!this.waitSelUserList.erro) {
              this.outVal = this.waitSelUserList.waitSelUserList;
              let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
              let a = commonHeader.extParam.specifyNextOpers;
              if (this.waitSelUserList.display == true && (a == undefined || a == '' || a == null)) {
                this.aaaa = true;  // 要选人
              }else{
                this.doSubmit();
              }
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: this.waitSelUserList.erro });
          }
        } else {
          this.doSubmit();
        }
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
        this.doEmpty();
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
      this.doEmpty();
    })
  }

  // 数据返回给后台
  doSubmit() {
    this.httpService.custManageDataLandHandle(this.customAllotDialogBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (s.extParam.specifyNextOpers) {
          delete s.extParam.specifyNextOpers
          let commonHeader = s;
          this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        }
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
        this.doEmpty();
      } else {
        let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (s.extParam.specifyNextOpers) {
          delete s.extParam.specifyNextOpers
          let commonHeader = s;
          this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        }
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
        this.doEmpty();
      }
    }, error => {
      let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
      if (s.extParam.specifyNextOpers) {
        delete s.extParam.specifyNextOpers
        let commonHeader = s;
        this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
      }
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
      this.doEmpty();
    })
  }

  // 点击叉号
  onHideClose(param) {
    if (param == 'org') {
      this.orgTreeDisplay = false;
    }
    if (param == 'display') {
      this.display = false;
    }
    if (param == 'people') {
      this.aaaa = false;
      this.doEmpty();
    }
  }

  // 关闭选人框
  outDisplayCall(event) {
    this.aaaa = false;
    this.display = false;
  }

  // 选完人后回调
  outValueCall(event) {
    this.outVal = event;
    let ss = '';
    this.outVal.forEach(item => {
      ss += item.tellerId + ','
    });
    let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    commonHeader.extParam['specifyNextOpers'] = ss;
    this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
    this.doSubmit();
  }

  addCallBean(event) {
    this.outValBean = event;
    this.tablelist = [];
  }

  // 清空
  doEmpty() {
    this.tablelist = [];
    this.chooseData = [];
    this.customAllotDialogBean = new CustomAllotExamBean();
    this.cusDoAllotBean.custNo = undefined;
    this.receiveOrgName = undefined;
    this.receiveOrgId = undefined;
  }

  // 选择员工
  chooseWorker() {
    if (this.receiveOrgId == '' || this.receiveOrgId == undefined) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '请先选择接收机构！' });
      return
    } else {
      this.display = true;
      this.ownerInValue = this.receiveOrgId;
    }
  }

  // 选择机构
  chooseOrg() {
    this.orgTreeDisplay = true;
  }

  // 机构树回调
  treeCall(param) {
    this.orgTreeDisplay = false;
    this.receiveOrgName = param.orgName;
    this.receiveOrgId = param.orgId;
    this.customAllotDialogBean.receiverName = '';
    this.customAllotDialogBean.receiver = '';
  }

  // 员工回调
  perInfor(item) {
    this.display = false;
    if (item.length > 0) {
      this.customAllotDialogBean.receiverName = item[0].tellerName;
      this.customAllotDialogBean.receiver = item[0].tellerId;
      this.customAllotDialogBean.receiveOrgName = item[0].orgName;
      this.customAllotDialogBean.receiveOrgId = item[0].orgId;
    }
  }

  // 按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id);
  }

  compileStr(cust){
    return this.commfunc.compileStr(cust)
  }

}
