import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { CustomManageComponent } from 'app/pages/tzb/custom/custom-marketing/custom-manage/custom-manage.component';
import { CusDoApplyComponent } from 'app/pages/tzb/custom/custom-marketing/custom-manage/cus-do-apply/cus-do-apply.component';
import { CusDoTtransferComponent } from 'app/pages/tzb/custom/custom-marketing/custom-manage/cus-do-transfer/cus-do-transfer.component';
import { CusDoAllotComponent } from 'app/pages/tzb/custom/custom-marketing/custom-manage/cus-do-allot/cus-do-allot.component';
import { BatchAllotComponent } from 'app/pages/tzb/custom/custom-marketing/custom-manage/batch-allot/batch-allot.component';
import { AdjustLogComponent } from 'app/pages/tzb/custom/custom-marketing/custom-manage/adjust-log/adjust-log.component';
import { WaiAllotComponent } from 'app/pages/tzb/custom/custom-marketing/custom-manage/wait-allot/wait-allot.component';

export const routes: Routes = [

    {
        path: '',
        component: CustomManageComponent,
        children: [
            { path: '', redirectTo: 'cus-do-apply', pathMatch: 'full' },
            {
                path: 'cus-do-apply', component: CusDoApplyComponent,
            },
            {
                path: 'cus-do-transfer', component: CusDoTtransferComponent,
            },
            {
                path: 'cus-do-allot', component: CusDoAllotComponent,
            },
            {
                path: 'batch-allot', component: BatchAllotComponent,
            },
            {
                path: 'wait-allot', component: WaiAllotComponent,
            },
            {
                path: 'adjust-log', component: AdjustLogComponent,
            },
        ]
    },

]

export const CusManageRouting: ModuleWithProviders = RouterModule.forChild(routes);
