/**
 * @author fanhualing 2018-5-9 客户申领
 */
import { Component, OnInit } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { CusOperationHttpService } from "app/pages/tzb/custom/http/custom-operation/custom-operation.http.service";
import { ConfirmDialogModule, ConfirmationService } from 'primeng/primeng';
import { Message } from 'primeng/components/common/api';
import { CusDoApplyExamBean, CusDoApplyBean } from 'app/pages/tzb/custom/custom-marketing/custom-manage/cus-do-apply/bean/CusDoApply.bean';

@Component({
  selector: 'app-cus-do-apply',
  templateUrl: './cus-do-apply.component.html',
  styleUrls: ['./cus-do-apply.component.scss'],
  providers: [CusOperationHttpService, Commfunc, ConfirmationService]
})
export class CusDoApplyComponent implements OnInit {

  workdeskApllyBean: CusDoApplyExamBean = new CusDoApplyExamBean();
  cusDoApplyBean: CusDoApplyBean = new CusDoApplyBean();
  msgs: Message[] = [];
  // 码值
  code: any;
  applyCert: any = [];
  cert_type: any = [];
  // 当前登录用户
  user: any;
  user1: any;
  userName: string = '';
  orgName: string = '';
  userId: string = '';
  orgId: string = '';
  // 数据
  chooseData: any = [];
  tablelist: any = [];
  // 审批流判断
  iskzh: string = '';
  iszlxr: string = '';
  iskfh: string = '';
  approveNo: string = '';
  dfCustno: string = '';
  dfOrg: string = '';
  // 选人
  waitSelUserList: any;
  outVal: any;
  aaaa: boolean = false;
  // 有主、无主判断
  OwnerDisplay: boolean = false;

  constructor(
    public httpService: CusOperationHttpService,
    public commfunc: Commfunc,
  ) {
    this.codeValues();
    this.cert_type = this.code['CERT_TYPE'];
    this.cert_type.forEach(element => {
      if (element.value != '') {
        this.applyCert.push(element);
      }
    });

    this.user1 = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    this.user = JSON.parse(this.commfunc.getSessionDataCH('chName'));
    if (this.user1.userId) {
      this.userId = this.user1.userId;
    }
    if (this.user1.orgId) {
      this.orgId = this.user1.orgId;
    }
    if (this.user.userName) {
      this.userName = this.user.userName;
    }
    if (this.user.orgName) {
      this.orgName = this.user.orgName;
    }
  }

  // 获取码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret;
  }

  ngOnInit() {
  }

  // 查询
  doQuery() {

    // 清空数据
    this.tablelist = [];
    this.chooseData = [];
    // 校验客户号
    var reg = /^[0-9]*$/;
    if (
      this.cusDoApplyBean.custNo == undefined ||
      this.cusDoApplyBean.custNo == ''
    ) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '请输入客户号!' });
      return
    }

    if (this.cusDoApplyBean.custNo != undefined) {
      if (this.cusDoApplyBean.custNo != '') {
        if (!reg.test(this.cusDoApplyBean.custNo)) {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: '客户号只能为数字' });
          return
        }
        if (this.cusDoApplyBean.custNo.length != 10) {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: '请输入10位数字的客户号' });
          return
        }
      }
    }

    this.cusDoApplyBean.operationType = '2'; // 操作类型-申领
    this.cusDoApplyBean.disType = ''; // 分配场景编号-按客户号分配
    this.httpService.queryCustManageCustInfo(this.cusDoApplyBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        if (data.custList.length > 0) {
          this.tablelist = data.custList;
          this.chooseData = this.tablelist;
          if (this.tablelist[0].ownerManagerName == '' || this.tablelist[0].ownerManagerName == null) {
            this.OwnerDisplay = false;
          } else {
            this.OwnerDisplay = true;
          }
        } else {
          this.chooseData = [];
        }
      }
      else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
    }
    )
  }

  // 我要申领
  doApply() {
    if (this.chooseData.length == 0) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '请选择客户！' });
      return
    } else {
      // 选择审批人
      this.doChoosePeople();
    }
  }

  // 提交服务
  doChoosePeople() {

    // 入参(‘申领’并按‘客户号分配’)标识
    this.workdeskApllyBean.operationType = '2';
    this.workdeskApllyBean.disType = '';
    // 接收人
    this.workdeskApllyBean.creater = this.userId;
    this.workdeskApllyBean.createrName = this.userName;
    this.workdeskApllyBean.createOrgId = this.orgId;
    this.workdeskApllyBean.createOrgName = this.orgName;
    this.workdeskApllyBean.receiver = this.userId;
    this.workdeskApllyBean.receiverName = this.userName;
    this.workdeskApllyBean.receiveOrgId = this.orgId;
    this.workdeskApllyBean.receiveOrgName = this.orgName;
    // 客户信息
    this.workdeskApllyBean.custList = this.chooseData;
    // 判断是否走审批
    this.httpService.custManageApprCandidate(this.workdeskApllyBean).subscribe(data => {
      if (data.returnCode.code == 'success') {

        this.dfCustno = data.dfCustno;
        this.dfOrg = data.dfOrg;
        this.approveNo = data.approveNo;
        this.iszlxr = data.iszlxr;
        this.iskzh = data.iskzh;
        this.iskfh = data.iskfh;
        this.workdeskApllyBean['approveNo'] = this.approveNo;
        this.workdeskApllyBean['iszlxr'] = this.iszlxr;
        this.workdeskApllyBean['iskzh'] = this.iskzh;
        this.workdeskApllyBean['iskfh'] = this.iskfh;
        this.workdeskApllyBean['dfCustno'] = this.dfCustno;
        this.workdeskApllyBean['dfOrg'] = this.dfOrg;

        if (data.approveNo == '1') {

          this.waitSelUserList = this.commfunc.query('KHSLLC', { iszlxr: this.iszlxr, dfCustno: this.dfCustno, iskfh: this.iskfh, iskzh: this.iskzh, dfOrg: this.dfOrg });
          if (!this.waitSelUserList.erro) {
            this.outVal = this.waitSelUserList.waitSelUserList;
            let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
            let a = commonHeader.extParam.specifyNextOpers;
            if (this.waitSelUserList.display == true && (a == undefined || a == '' || a == null)) {
              this.aaaa = true;  // 要选人
            } else {
              this.doSubmit();
            }
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: this.waitSelUserList.erro });
          }
        } else {
          this.doSubmit();
        }
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
        this.doEmpty();
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
      this.doEmpty();
    })
  }

  // 点击叉号
  onHideClose() {
    this.aaaa = false;
    this.doEmpty();
  }

  // 关闭选人框
  outDisplayCall(event) {
    this.aaaa = false;
    this.doEmpty();
  }

  // 选完人后回调
  outValueCall(event) {
    this.outVal = event;
    let ss = '';
    this.outVal.forEach(item => {
      ss += item.tellerId + ','
    });
    let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    commonHeader.extParam['specifyNextOpers'] = ss;
    this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
    this.doSubmit();
  }

  // 数据返回给后台
  doSubmit() {
    this.httpService.custManageDataLandHandle(this.workdeskApllyBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (s.extParam.specifyNextOpers) {
          delete s.extParam.specifyNextOpers
          let commonHeader = s;
          this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        }
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
        this.doEmpty();
      } else {
        let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (s.extParam.specifyNextOpers) {
          delete s.extParam.specifyNextOpers
          let commonHeader = s;
          this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        }
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
        this.doEmpty();
      }
    }, error => {
      let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
      if (s.extParam.specifyNextOpers) {
        delete s.extParam.specifyNextOpers
        let commonHeader = s;
        this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
      }
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
      this.doEmpty();
    })
  }

  // 清空
  doEmpty() {
    this.tablelist = [];
    this.chooseData = [];
    this.workdeskApllyBean = new CusDoApplyExamBean();
    this.cusDoApplyBean.custNo = undefined;
  }

  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id);
  }

  compileStr(cust){
    return this.commfunc.compileStr(cust)
  }
}
